/**
 * API slu≈æba pro LP ƒçerp√°n√≠ na faktur√°ch
 * Endpoint: /faktury/lp-cerpani/*
 * 
 * ‚ö†Ô∏è D≈ÆLE≈ΩIT√â: API oƒçek√°v√° token a username v BODY (ne v headerech!)
 */

import axios from 'axios';

const api = axios.create({
  baseURL: process.env.REACT_APP_API2_BASE_URL,
  headers: { 'Content-Type': 'application/json' }
});

/**
 * Ulo≈æit LP ƒçerp√°n√≠ na faktu≈ôe
 * @param {number} fakturaId - ID faktury
 * @param {Array} lpCerpani - Array of {lp_cislo, lp_id, castka, poznamka}
 * @param {string} token - Auth token (p≈ôedat z AuthContext)
 * @param {string} username - Username (p≈ôedat z AuthContext)
 * @returns {Promise} Response s ulo≈æen√Ωmi daty
 */
export async function saveFakturaLPCerpani(fakturaId, lpCerpani, token, username) {
  if (!token || !username) {
    throw new Error('Chyb√≠ autentizaƒçn√≠ √∫daje (token nebo username)');
  }

  const response = await api.post('/faktury/lp-cerpani/save', {
    token,
    username,
    faktura_id: fakturaId,
    lp_cerpani: lpCerpani
  });

  return response.data;
}

/**
 * Naƒç√≠st LP ƒçerp√°n√≠ pro fakturu
 * @param {number} fakturaId - ID faktury
 * @param {string} token - Auth token (p≈ôedat z AuthContext)
 * @param {string} username - Username (p≈ôedat z AuthContext)
 * @returns {Promise} Response s LP ƒçerp√°n√≠m
 */
export async function getFakturaLPCerpani(fakturaId, token, username) {
  if (!token || !username) {
    throw new Error('Chyb√≠ autentizaƒçn√≠ √∫daje (token nebo username)');
  }

  const response = await api.post('/faktury/lp-cerpani/get', {
    token,
    username,
    faktura_id: fakturaId
  });

  return response.data;
}
